  float: left;
  margin-bottom: 13px;
}
.xg_Div {
  width: 100px;
  height: 40px;
  line-height: 40px;
  float: left;
}
.xg_input {
  font-size: 16px;
  font-weight: bold;
  color: #888888;
  margin-left: 10px;
  border: none;
}
.sahnchu_p {
  width: 80%;
  text-align: center;
  margin: auto;
  font-size: 15px;
  color: #888888;
}
.tanchukuang {
  margin-top: 6.5%;
}
.deluser_left {
  width: 85px;
  height: 42px;
  font-size: 15px;
  line-height: 40px;
  text-align: center;
  padding: 0px;
  font-weight: bold;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  float: left;
  box-sizing: border-box;
  float: left;
  background-color: rgb(233, 230, 230);
  color: #696767;
  padding: 0px;
}
.deluser_right {
  float: left;
  width: 230px;
  height: 42px;
  line-height: 42px;
  font-size: 18px;
  font-weight: bold;
}
.de {
  border: none;
}
</style>



